function generateArrayOfYears() {
    let current = new Date().getFullYear()
    let max = current + 10;
    let years = []

    for (var i = current - 10; i <= max; i++) {
        years.push(i)
    }
    return years
}


function createProtoObjectFromTable(rows, columns) {
    let protoObject = {};

    for (var i = 0; i < rows.length; i++) {
        let num = i + 2
        protoObject["A" + num.toString()] = rows[i];
    }

    for (var i = 0; i < columns.length; i++) {
        protoObject[alphabet[i + 1] + "1"] = columns[i] + "";
    }

    return protoObject;
}

