let modalForm = [
    { name: "title", title: "ÐÐ°Ð·Ð²Ð°Ð½Ð¸Ðµ", type: "text" },
    { name: "comment", title: "ÐÐ¾Ð¼Ð¼ÐµÐ½ÑÐ°ÑÐ¸Ð¹", type: "textarea" }
];

function PostFormHook(props) {
    const { register, handleSubmit, reset, watch, errors } = useForm({
        defaultValues: { title: props.title }
    });

    React.useEffect(() => { reset({ title: props.title }) }, [props.title]);

    return <form onSubmit={handleSubmit(props.setFormObject)}>
        {modalForm.map(item =>
            <InputGroup className="mb-3" size="sm" key={item.name}>
                <InputGroup.Prepend><InputGroup.Text style={{ width: '8rem' }}>{item.title}</InputGroup.Text></InputGroup.Prepend>
                <FormControl
                    key={item.name}
                    name={item.name}
                    type={item.type}
                    ref={register}
                    as={item.type === "textarea" ? "textarea" : "input"}
                />
            </InputGroup>
        )}
        <Button variant="outline-secondary" type="submit" size="sm">Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ</Button>
    </form>
}




